.modalBackground{
    width: 100vm;
    height: 100vm;
    background-color: rgba(200, 200, 200);
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
}
.modalContainer{
    width: 250px;
    height: 300px;
    border-radius: 12px;
    background-color: white;
    box-shadow: rgba(0, 0 ,0, 0.35) 0px 5px 15px;
    display: flex;
    flex-direction: column;
    padding: 25px;
    ;
}
.modalContainer .title{
    display: inline-block;
    text-align: center;
    margin-top: 10px;
}
.modalContainer .body{
    flex:  50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.7 rem;
    text-align: center
    ;
}
.titleCloseBtn{
    display: flex;
    justify-content: flex-end;
}
.titleCloseBtn button{
    background-color: transparent;
    border: none;
    font-size: 25px;
    cursor: pointer;
}
.modalContainer .footer{
    flex: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.modalContainer .footer button{
    width: 150px;
    height: 45px;
    margin: 10px;
    border: none;
    background-color: cornflowerblue;
    color: white;
    border-radius: 8px;
    font-size: 20px;
    cursor: pointer;
}
#cancleBtn{
    background-color: crimson;
}